/* about */
.about{
  background-color: rgba(236, 214, 214, 0.6);
  justify-content:space-around;
  flex-direction:row;
  margin:20px;
  border-radius:10px;
  padding:25px;
}

.navbar {
  width: 100%;
  background-color: #f1845f;
  height: 6vh;
  display: flex;
  justify-content: flex-start; }

.title {
  font-size: 50px;
  font-family: 'Lato', sans-serif;
  margin-top: 15px; }

.buttons {
  display: flex;
  align-items: center; }

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.form-inline{
  display:flex;
  justify-content: center;
  
}

form {
  display:flex;
  flex-flow: column wrap;
  align-items: center;
  background-color: transparent;
  padding: 100px 40px;
  border: 1px solid #112b72;

   label {
     margin: 20px;
     padding: 10px 10px;
   }
}


