.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.form-inline{
  display:flex;
  justify-content: center;
  
}

form {
  display:flex;
  flex-flow: column wrap;
  align-items: center;
  background-color: transparent;
  padding: 100px 40px;
  border: 1px solid #112b72;

   label {
     margin: 20px;
     padding: 10px 10px;
   }
}

